
const routers = [
    {
        path: '/',
        meta: {
            title: ''
        },
        component: (resolve) => require(['./views/market/index.vue'], resolve)
    },
    {
        path: '/market',
        name: 'market',
        meta: {
            title: ''
        },
        component: (resolve) => require(['./views/market/index.vue'], resolve)
    }
];
export default routers;


